import React from 'react';
import { SlSocialGithub, SlShareAlt } from 'react-icons/sl';
// import "./projectcard.css";
import "./projectcard.scss";

const ProjectCard = (props) => {
    return (
        <div className='project-card'>
            <img className='card-img' alt='card' src=''/>
            <div className='card-container'>
                <h4 className='card-title'>{props.title}</h4>
                <p className='card-description'>{props.desc}</p>
                <div className='card-icons'>
                    {props.lang.map((val, id) => {
                        return(
                            <div key={id} className='card-chip'>
                                <p>{val}</p>
                            </div>
                        )
                    })}
                </div>
                <div className='card-links'>
                    <a target='_blank' rel='noreferrer' title={props.git} href={props.git}><SlSocialGithub/></a>
                    <a target='_blank' rel='noreferrer' title={props.web} href={props.web}><SlShareAlt/></a>
                </div>
            </div>
        </div>
    );
}

export default ProjectCard;
