import React from 'react';
import Photo from '../../assets/images/perfil.png';
import { Typewriter } from 'react-simple-typewriter';

import "./home.scss";

const Home = () => {
    return (
        <section id='home'>
            <div className='container-lg home'>
                <div className='col-2 home-desc'>
                    <h4 className='header-name'>Hola, soy Tito</h4>
                    <h1 className='header-title'>
                        <Typewriter 
                            className='header-title'
                            words={["Front-End", "Back-End", "Full-Stack"]}
                            loop={5}
                            typeSpeed={70}
                            deleteSpeed={50}
                            delaySpeed={1000}>
                        </Typewriter>
                    </h1>
                    <h3 className='header-subtitle'>Developer</h3>
                    <p className='header-description'>Vivo en constante aprendizaje para ir mejorando mis habilidades como programador</p>
                </div>
                <div className='col-2 home-photo'>
                    <img className='my-photo' alt='mi persona' src={Photo}/>
                    <div className='blob'></div>
                </div>
            </div>
        </section>
    );
}

export default Home;
