import React from 'react';
import ProjectCard from '../../components/card/project/ProjectCard.jsx';
import Data from './Data';
import { motion } from 'framer-motion';
import { SlArrowRight } from "react-icons/sl";

import "./projects.scss";

const faceInAnimationsVariants = {
    initial: {
        opacity: 0,
        y: 100,
    },
    animate: (id) => ({
        opacity: 1,
        y: 0,
        transition: {
            delay: 0.1 * id,
            ease: "linear"
        }
    }),
};

const Projects = () => {
    return (
        <section id='projects'>
            <div className='container-lg projects'>
                <div className='section-header'>
                    <h4>Projectos</h4>
                    <p>Explora mis creaciones.</p>
                </div>
                <div className='projects-container'>
                    {Data.map((val, id) => {
                        return (
                            <motion.div
                                key={id}
                                variants={faceInAnimationsVariants}
                                initial="initial"
                                whileInView="animate"
                                viewport={{
                                    once: true,
                                }}
                                custom={id}
                                >
                                <ProjectCard key={id} title={val.title} desc={val.desc} lang={val.lang}/>
                            </motion.div>
                        )
                    })}
                </div>
                <div className='projects-more'>
                    <p>Ver ms <SlArrowRight className='arrow'/></p>
                </div>
            </div>
        </section>
    );
}

export default Projects;
