import React, { useEffect, useState, useMemo, useCallback } from "react";
import { FiDownload } from "react-icons/fi";
import { motion } from "framer-motion";
import {
  SlSocialInstagram,
  SlSocialGithub,
  SlSocialLinkedin,
} from "react-icons/sl";
import Photo from "../../assets/images/profile_V6.svg";

import "./about.scss";

const About = () => {
  const [isVisible, setIsVisible] = useState([false, false, false]);
  const [scrollPosition, setScrollPosition] = useState(0);

  const paragraphRefs = useMemo(
    () => [React.createRef(), React.createRef(), React.createRef()],
    []
  );

  const paragraphTexts = [
    "Hola! Mi nombre es Luis Daniel y soy un apasionado de la informtica en constante proceso de formacin como programador. Actualmente, estoy cursando el grado de Ingeniera Informtica y el grado superior DAM. Adems, he completado con xito un bootcamp de 'Full Stack' en OpenBootcamp. Mi pasin por aprender y mi dedicacin al 100% en cada proyecto son rasgos distintivos de mi personalidad.",
    "Antes de embarcarme en el mundo de la programacin, trabaj como administrativo-comercial en una empresa de telecomunicaciones. Durante esta etapa, desarroll habilidades destacadas en la resolucin de problemas, la capacidad para trabajar bajo presin y las relaciones interpersonales. Sin embargo, fue durante ese perodo cuando descubr mi verdadera vocacin: la informtica. Desde entonces, he estado en constante crecimiento y bsqueda de oportunidades para aplicar mis conocimientos y seguir aprendiendo nuevas tecnologas.",
    "Actualmente, estoy en la bsqueda de nuevas oportunidades que me permitan seguir creciendo tanto a nivel personal como profesional. Me encantara formar parte de proyectos desafiantes que me brinden la posibilidad de aplicar mis habilidades y adquirir nuevos conocimientos en el campo de la programacin. Estoy disponible para entrevistas y responder cualquier pregunta que puedas tener.",
  ];

  const activateParagraph = useCallback(
    (index) => {
      if (!isVisible[index]) {
        setIsVisible((prev) => {
          const newVisible = [...prev];
          newVisible[index] = true;
          return newVisible;
        });
      }
    },
    [isVisible]
  );

  useEffect(() => {
    const handleScroll = () => {
      const newPosition = window.scrollY;

      if (newPosition > scrollPosition) {
        for (let i = 0; i < paragraphRefs.length; i++) {
          const paragraphRef = paragraphRefs[i].current;
          if (paragraphRef) {
            const paragraphPosition = paragraphRef.offsetTop;
            const windowHeight = window.innerHeight;

            if (scrollPosition >= paragraphPosition - windowHeight / 1.2) {
              activateParagraph(i);
            }
          }
        }
      }

      setScrollPosition(newPosition);
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [scrollPosition, paragraphRefs, activateParagraph]);

  const variants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0, transition: { duration: 0.6 } },
  };

  return (
    <section id="about-me">
      <div className="container-lg about">
        <div className="section-header">
          <h4>Sobre m</h4>
          <p>Mi presente y mi futuro.</p>
        </div>
        <div className="about-container">
          <div className="col-2 about-photo">
            <img className="photo" src={Photo} alt="Mi persona" />
            <div className="about-social">
              <a className="social-icons" 
                target="_blank"
                rel="noreferrer"
                title="GitHub"
                href="https://github.com/TitoDevs">
                <SlSocialGithub />
              </a>
              <a className="social-icons"
                target="_blank"
                rel="noreferrer"
                title="Instagram"
                href="https://www.instagram.com/tito.dev/">
                <SlSocialInstagram />
              </a>
              <a className="social-icons"
                target="_blank"
                rel="noreferrer"
                title="LinkedIn"
                href="https://www.linkedin.com/in/ldanielgg/">
                <SlSocialLinkedin />
              </a>
              <button className="btn-cv">
                <a href="../../assets/pdf/CV_LuisDaniel.pdf" download={"LuisDaniel_CV.pdf"}>Descargar CV</a>
                <FiDownload />
              </button>
            </div>
          </div>
          <div className="col-2 about-desc">
            {paragraphTexts.map((text, index) => (
              <motion.p
                key={index}
                initial="hidden"
                animate={isVisible[index] ? "visible" : "hidden"}
                variants={variants}
                ref={paragraphRefs[index]}
                dangerouslySetInnerHTML={{ __html: text }}
              />
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;
