import React, { useState, useEffect } from 'react';
import { SlArrowUp } from "react-icons/sl";

import "./scrolltotopbutton.scss";

const ScrollToTopButton = () => {
  const [isVisible, setIsVisible] = useState(false);

  // Mostrar el botn cuando el usuario se desplace hacia abajo
  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 100) {
        setIsVisible(true);
      } else {
        setIsVisible(false);
      }
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  // Funcin para desplazar al principio de la pgina
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth', // Animacin de desplazamiento suave
    });
  };

  return (
    <div className={`scroll-to-top-button ${isVisible ? 'visible' : ''}`}>
      <button onClick={scrollToTop}><SlArrowUp/></button>
    </div>
  );
};

export default ScrollToTopButton;
