import React from 'react';
import Data from './Data';
import { VerticalTimeline, VerticalTimelineElement } from 'react-vertical-timeline-component';
import "react-vertical-timeline-component/style.min.css";
import { SlGraduation } from 'react-icons/sl';

import "./experience.scss";

const Experience = () => {
    return (
        <section id='experience'>
            <div className='container-lg experience'>
                <div className='section-header'>
                    <h4>Educacin</h4>
                    <p >Resumen de mi trayectoria y experiencias de vida.</p>
                </div>
                <VerticalTimeline>
                    {Data.map(element => {
                        return (
                            <VerticalTimelineElement
                                key={element.key}
                                date={element.date}
                                dateClassName="experience-date"
                                iconStyle={{backgroundColor: "#161927"}}
                                icon={<SlGraduation style={{color:"#ffbd59"}}/>}
                            >
                                <h5 className='experience-title'>{element.title}</h5>
                                <p className='experience-desc'>{element.desc}</p>
                            </VerticalTimelineElement>
                        )
                    })}
                </VerticalTimeline>
            </div>
        </section>
    );
}

export default Experience;
