import React, { useEffect, useState } from "react";
import Navbar from "./components/navbar/Navbar";
import Footer from "./components/footer/Footer";
import Projects from "./pages/projects/Projects";
import Home from "./pages/home/Home";
import About from "./pages/about/About";
import Experience from "./pages/experience/Experience";
import Services from "./pages/services/Services";
import Contact from "./pages/contact/Contact";
import ScrollToTopButton from "./components/scroll/ScrollToTopButton";
import SplashScreen from "./components/splashscreen/SplashScreen";

import "./index.css";

function App() {
  const [loading, setLoading] = useState(true);
  const [showApp, setShowApp] = useState(false);

  useEffect(() => {
    // Simular una pantalla de carga
    const loadingTimeout = setTimeout(() => {
      setLoading(false);
    }, 2000);

    // Mostrar la aplicacin despus de la pantalla de carga
    const showAppTimeout = setTimeout(() => {
      setShowApp(true);
    }, 2000); // Aument el tiempo para mostrar la aplicacin

    // Limpia los temporizadores al desmontar el componente
    return () => {
      clearTimeout(loadingTimeout);
      clearTimeout(showAppTimeout);
    };
  }, []);

  return (
    <div className={`App ${showApp ? 'fade-in' : 'fade-out'}`}>
      {loading ? (
        <SplashScreen />
      ) : (
        <>
          <Navbar />
          <Home />
          <Services />
          <About />
          <Experience />
          <Projects />
          <Contact />
          <Footer />
          <ScrollToTopButton />
        </>
      )}
    </div>
  );
}

export default App;
