import React, { useState, useEffect } from "react";
import { Resend } from "resend";
import "./contact.scss";

const Contact = () => {
  const [formData, setFormData] = useState({
    nombre: "",
    email: "",
    description: "",
  });

  const [enviado, setEnviado] = useState(false);
  const [enviando, setEnviando] = useState(false);

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
  
    setEnviando(true);
  
    try {
      const resend = new Resend('re_FKXb4CJE_B3hszMHcH6RHQJ2BE7qQKve5');
  
      await resend.emails.send({
        to: "daniel.guardado@outlook.es",
        from: "onboarding@resend.dev",
        text: "Congrats on sending your first email!",
        html: "<p>Congrats on sending your <strong>first email</strong>!</p><hr /><p style=\"color:#898989;font-size:12px;\">2261 Market Street #5039 - San Francisco, CA 94114</p>",
        subject: "Hello World",
        bcc: [],
        cc: []
      });
  
      setEnviado(true);
    } catch (error) {
      console.error('Error al enviar el correo:', error);
    } finally {
      setEnviando(false);
    }
  };
  

  useEffect(() => {
    if (enviado) {
      setFormData({
        nombre: "",
        email: "",
        description: "",
      });
    }
  }, [enviado]);

  return (
    <section id="contact">
      <div className="container-lg contact">
        <div className="section-header">
          <h4>Contacto</h4>
          <p>Nos tomamos un caf?</p>
        </div>
        <div className="section-container">
          <div className="contact-form">
            <form onSubmit={handleSubmit}>
              <div className="container-contact">
                <div className="contact-left">
                  <label htmlFor="nombre">Nombre</label>
                  <input
                    type="text"
                    id="nombre"
                    name="nombre"
                    value={formData.nombre}
                    onChange={handleInputChange}
                    required
                  />

                  <label htmlFor="email">Email</label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    required
                  />
                </div>
                <div className="contact-right">
                  <label htmlFor="description">Descripcin</label>
                  <textarea
                    id="description"
                    name="description"
                    value={formData.description}
                    rows={6}
                    onChange={handleInputChange}
                    required
                  />
                </div>
              </div>
              <button
                className={`bt-submit ${enviando ? "enviando" : enviado ? "enviado" : ""}`}
                type="submit"
                disabled={enviado || enviando}
              >
                {enviando ? (
                  <div className="spinner"></div>
                ) : (
                  enviado ? "Enviado" : "Enviar"
                )}
              </button>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Contact;
