import React, { useState, useEffect } from "react";
import { Link } from "react-scroll";
import {
  SlSocialGithub,
  SlSocialLinkedin,
  SlSocialInstagram,
  SlHome,
  SlUser,
  SlEnvolope,
  SlGraduation,
} from "react-icons/sl";
import Logo from "../../assets/images/logo.svg";

import "./navbar.scss";

const Navbar = () => {
  const [isMenuOpen, setMenuOpen] = useState(false);
  const [hasScrolled, setHasScrolled] = useState(false);

  const toggleMenu = () => {
    setMenuOpen(!isMenuOpen);
  };

  const styleToggle = isMenuOpen ? "menu-toggle open" : "menu-toggle";
  const styleNavItems = isMenuOpen ? "nav-items open" : "nav-items";
  const styleScrolled = hasScrolled ? "scrolled" : "";

  useEffect(() => {
    const handleResize = () => {
      if (window.scrollY > 100) {
        setHasScrolled(true);
      } else {
        setHasScrolled(false);
      }

      if (window.innerWidth > 768) {
        setMenuOpen(false);
      }
    };

    window.addEventListener("scroll", handleResize);
    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
      window.removeEventListener("scroll", handleResize);
    };
  }, []);

  return (
    <nav className={styleScrolled}>
      <div className="nav-container">
        <Link
          className="nav-logo"
          to="home"
          spy={true}
          smooth={true}
          offset={0}
          duration={500}
        >
          <img src={Logo} alt="logo" />
        </Link>
        <ul className={styleNavItems}>
          <li>
            <Link
              to="home"
              spy={true}
              smooth={true}
              offset={0}
              duration={500}
              onClick={toggleMenu}
            >
              <SlHome className="nav-items-icon" size={24} />
              Home
            </Link>
          </li>
          <li>
            <Link
              to="about-me"
              spy={true}
              smooth={true}
              offset={-10}
              duration={500}
              onClick={toggleMenu}
            >
              <SlUser className="nav-items-icon" size={24} />
              Sobre m
            </Link>
          </li>
          <li>
            <Link
              to="experience"
              spy={true}
              smooth={true}
              offset={-10}
              duration={500}
              onClick={toggleMenu}
            >
              <SlUser className="nav-items-icon" size={24} />
              Educacin
            </Link>
          </li>
          <li>
            <Link
              to="projects"
              spy={true}
              smooth={true}
              offset={-10}
              duration={500}
              onClick={toggleMenu}
            >
              <SlGraduation className="nav-items-icon" size={24} />
              Proyectos
            </Link>
          </li>
          <li>
            <Link
              to="contact"
              spy={true}
              smooth={true}
              offset={-10}
              duration={500}
              onClick={toggleMenu}
            >
              <SlEnvolope className="nav-items-icon" size={24} />
              Contacto
            </Link>
          </li>
        </ul>
        <ul className="nav-social-icons">
          <li>
            <a
              target="_blank"
              rel="noreferrer"
              title="LinkedIn"
              href="https://www.linkedin.com/in/ldanielgg/"
            >
              <SlSocialLinkedin />
            </a>
          </li>
          <li>
            <a
              target="_blank"
              rel="noreferrer"
              title="GitHub"
              href="https://github.com/TitoDevs"
            >
              <SlSocialGithub />
            </a>
          </li>
          <li>
            <a
              target="_blank"
              rel="noreferrer"
              title="Instagram"
              href="https://www.instagram.com/tito.dev/"
            >
              <SlSocialInstagram />
            </a>
          </li>
        </ul>
        <div className={styleToggle} onClick={toggleMenu}>
          <span />
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
